import { validateForms } from '../functions/validate-forms';


const rulesModal = [
  {
    ruleSelector: '.input-name',
    rules: [
      {
        rule: 'minLength',
        value: 2,
        errorMessage: '2 !',
      },
      {
        rule: 'required',
        value: true,
        errorMessage: ' !',
      }
    ]
  },
  {
    ruleSelector: '.input-tel',
    tel: true,
    telError: '  ',
    rules: [
      {
        rule: 'required',
        value: true,
        errorMessage: ' !'
      }
    ]
  },
];



const afterForm = () => {
};

validateForms('.form--modal', rulesModal, afterForm);


